import { FormattedMessage, injectIntl } from 'react-intl';
import React, {Component} from 'react'
import {actions} from 'mirrorx';
import queryString from "query-string";
import Header from 'components/Header';
import Button from 'components/Button';

import './index.less';

class IndexView extends Component {


    onBack = () => {
        // routing 
        actions.routing.goBack();
    }

    render() {
        const _this = this;
        const searchObj = queryString.parse(_this.props.location.search);
        const {code, name, sexEnumValue, levelName} = searchObj;
        return (
            <div className="employee">
                <Header title={this.props.intl.formatMessage({id:"js.com.Ind10.0001", defaultMessage:"A1"})} back={true}>
                    {/*<div className='head-btn'>*/}
                        {/*<Button shape="border" className="ml8" onClick={_this.onBack}></Button>*/}
                    {/*</div>*/}
                </Header>
                <div className="content">
                    <div className="item">
                        <span><FormattedMessage id="js.com.Ind10.0002" defaultMessage="" /></span>
                        <span>{code}</span>
                    </div>
                    <div className="item">
                        <span><FormattedMessage id="js.com.Ind10.0003" defaultMessage="" /></span>
                        <span>{name}</span>
                    </div>
                    <div className="item">
                        <span><FormattedMessage id="js.com.Ind10.0004" defaultMessage="" /></span>
                        <span>{sexEnumValue}</span>
                    </div>
                    <div className="item">
                        <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<FormattedMessage id="js.com.Ind10.0005" defaultMessage="" /></span>
                        <span>{levelName}</span>
                    </div>
                </div>
            </div>

        )

    }
}
export default injectIntl(IndexView)